// import axios from 'axios'
export default {
    data(){
        return{
            Component: 'ShopPage',
            MainInfo: null,
            ProductCounter: 0,
            carouselCounter: true
        }
    },
    watch: {
        $route() {
            if(process.browser){
                console.log(Object.keys(this.$route.query)[0]?.split('+').join(' '));
            if(Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?').length > 1 && Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?')[0] == 'FCat'){
                this.$store.commit('FirstCategoryFilter', Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?')[1])
            } else if(Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?').length > 1 && Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?')[0] == 'SCat') {
                this.$store.commit('SecondCategoryFilter', Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?')[1])
            } else if(Object.keys(this.$route.query).length > 0){
                this.$store.commit('ThirdCategoryFilter', Object.keys(this.$route.query)[0]?.split('+').join(' '))
            }
            }
        },
        Products(newV){
            if(this.carouselCounter){
                setTimeout(() => {
                    console.log('LOL')
                    this.carouselCounter = false
                    $.HSCore.components.HSSlickCarousel.init('.js-slick-carousel');
                }, 1000);
            }
        }
    },
// async created(){
//     var self = this
//     await axios.get('url')
//     .then( response => {
//         self.MainInfo = response.data.info
//     })
// },
    updated(){
        $.HSCore.components.HSUnfold.init($('[data-unfold-target]'), {
            beforeClose: function () {
                $('#hamburgerTrigger').removeClass('is-active');
            },
            afterClose: function() {
                $('#headerSidebarList .collapse.show').collapse('hide');
            }
        });

        $('#headerSidebarList [data-toggle="collapse"]').on('click', function (e) {
            e.preventDefault();

            var target = $(this).data('target');

            if($(this).attr('aria-expanded') === "true") {
                $(target).collapse('hide');
            } else {
                $(target).collapse('show');
            }
        });
        setTimeout(() => {
            this.RangeInit()
            this.RangeInit2()
        }, 4000);
    },
    mounted() {
        if(Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?').length > 1 && Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?')[0] == 'FCat'){
            this.$store.commit('FirstCategoryFilter', Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?')[1])
        } else if(Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?').length > 1 && Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?')[0] == 'SCat') {
            this.$store.commit('SecondCategoryFilter', Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?')[1])
        } else if(Object.keys(this.$route.query).length > 0){
            this.$store.commit('ThirdCategoryFilter', Object.keys(this.$route.query)[0]?.split('+').join(' '))
        }
        $(document).on('ready', function () {
            // initialization of unfold component
            $.HSCore.components.HSUnfold.init($('[data-unfold-target]'), {
                beforeClose: function () {
                    $('#hamburgerTrigger').removeClass('is-active');
                },
                afterClose: function() {
                    $('#headerSidebarList .collapse.show').collapse('hide');
                }
            });

            $('#headerSidebarList [data-toggle="collapse"]').on('click', function (e) {
                e.preventDefault();

                var target = $(this).data('target');

                if($(this).attr('aria-expanded') === "true") {
                    $(target).collapse('hide');
                } else {
                    $(target).collapse('show');
                }
            });
        });
    },
    methods: {
        NextProductsPage(){
            if(this.ProductCounter+2 <= Math.ceil(this.Products.length / 50)) this.ProductCounter += 1
        },
        PrevProductsPage(){
            if(this.ProductCounter >= 1) this.ProductCounter -= 1
        },
        ChProductsCounter(e){
            if(e.data != null) this.ProductCounter = e.data + 1
        },
        Sort() {
            console.log(2);
            if(process.browser){
            var Checkboxes = Array.prototype.slice.call(document.querySelectorAll(".chCat"));
            var result = []

            Checkboxes.forEach(element => {
                if(element.checked == true) {
                    result.push([element.name.split('/')[0],element.name.split('/')[1]])
                }
            });

            // CategoryFilter = this.Products.filter(Product => {
            //     for (let index = 0; index < result.length; index++) {
            //     if(Product.CategoryPoints.indexOf(result[index]) == -1) {
            //         return null
            //     }
            //     }
            //     return Product
            // })
            if(window.screen.width >= 1200) {
                var MinPrice = document.getElementById("input-left").value * 1000;
                var MaxPrice = document.getElementById("input-right").value * 1000;
            } else {
                var MinPrice = document.getElementById("input-left2").value * 1000;
                var MaxPrice = document.getElementById("input-right2").value * 1000;
            }
            console.log(result);
            console.log(MinPrice, MaxPrice);
            // PriceFilter = this.ProductsCopy.filter(Product => Product.price >= MinPrice && Product.price <= MaxPrice})
            // this.ProductsCopy = PriceFilter
            console.log(Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?'));
            if(Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?').length > 1 && Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?')[0] == 'FCat'){
                this.$store.commit('FilterProducts', [result, [MinPrice, MaxPrice], ['FCat', Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?')[1]]])
            } else if(Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?').length > 1 && Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?')[0] == 'SCat'){
                this.$store.commit('FilterProducts', [result, [MinPrice, MaxPrice], ['SCat', Object.keys(this.$route.query)[0]?.split('+').join(' ').split('?')[1]]])
            } else {
                this.$store.commit('FilterProducts', [result, [MinPrice, MaxPrice], ['TCat', Object.keys(this.$route.query)[0]?.split('+').join(' ')]])
            }
            }
        },
        SelectSorting(select){
            const option = select.target.selectedIndex
            switch (option) {
                case 0:
                    this.DefaultSorting()
                    break;
                case 1:
                    this.SortByPopularity()
                    break;
                case 2:
                    this.SortByPrice('up')
                    break;
                case 3:
                    this.SortByPrice('down')
                    break;
                default:
                    this.DefaultSorting()
                    break;
                }
        },
        AddToCartSwiper(el){
            this.$store.commit('cart/cartChange', el)
        },
        localizeFilter(key, key2, key3) {
            if(key3) return this.$store.state.lang.locales?.[this.$store.state.lang.lang]?.[this.Component]?.[key]?.[key2]?.[key3] || ``
            else if(key2) return this.$store.state.lang.locales?.[this.$store.state.lang.lang]?.[this.Component]?.[key]?.[key2]|| ``
            else return this.$store.state.lang.locales?.[this.$store.state.lang.lang]?.[this.Component]?.[key] || ``
        },
        RangeInit(){
            let inputLeft = document.getElementById("input-left");
            let inputRight = document.getElementById("input-right");
            let range = document.querySelector(".slider > .range");
            let priceFrom = document.querySelector(".price-from");
            let priceTo = document.querySelector(".price-to");
            function setLeftValue() {
            let _this = inputLeft,
                min = parseInt(_this.min),
                max = parseInt(_this.max);

            _this.value = Math.min(
                parseInt(_this.value),
                parseInt(inputRight.value) - 50
            );
            priceFrom.textContent = `from :${_this.value * 1000} тг.`;

            let percent = ((_this.value - min) / (max - min)) * 100;

            range.style.left = percent + "%";
            }

            setLeftValue();

            function setRightValue() {
            let _this = inputRight,
                min = parseInt(_this.min),
                max = parseInt(_this.max);

            _this.value = Math.max(parseInt(_this.value), parseInt(inputLeft.value) + 50);
            priceTo.textContent = `to: ${_this.value * 1000} тг.`;

            let percent = ((_this.value - min) / (max - min)) * 100;

            range.style.right = 100 - percent + "%";
            }

            setRightValue();
                inputLeft.addEventListener("input", setLeftValue);
                inputRight.addEventListener("input", setRightValue);

                inputLeft.addEventListener("mouseover", (e) => {
                inputLeft.classList.add("hover");
                });
                inputLeft.addEventListener("mouseout", (e) => {
                inputLeft.classList.remove("hover");
                });
